import React, { useEffect, useState } from 'react'

const useFetchCEP = () => {

    const [address,setAddress] = useState('')
    const [loading,setLoading] = useState('')
    const [error,setError] = useState('')
    const [clearMemory,setClearMemory] = useState(false)
    const isNumber = (numero) => /^[0-9]+$/.test(numero)
    const isValid = (cep) => cep.length == 8 && isNumber(cep)

    const clearMemoryLeak = () =>{
        if(clearMemory) return;
    }

    const fetchCep = async (cep) =>{
        clearMemoryLeak()
        setLoading(true)
        if(isValid(cep)){
            try {
                const url = `https://viacep.com.br/ws/${cep}/json/`
                const reqCEP = await fetch(url)
                const json = await reqCEP.json()
                setAddress(json)
                console.log('foi')
            } catch (error) {
                console.log("oi",error.message)
            } 
            setLoading(false)
        }
       
       
    }

useEffect(() => {
    return () => setClearMemory(true)
})

return {fetchCep,address}
}

export default useFetchCEP