// hooks
import {useEffect, useRef, useState} from 'react'

import useLogoff from '../../hooks/useLogoff'

// context
import { useAuth } from '../../context/AuthContext'

// Router
import { Link } from 'react-router-dom'

// styles
import styles from './Header.module.css'

// images
import logo from '../../assets/images/Logo.png'
import useReadCart from '../../hooks/dbHooks/useReadCart'
//components
import PrimaryNav from './primary/PrimaryNav'
import ToggleNav from './toggle/ToggleNav'
import Search from '../Search/Search'

const Header = () => {

const checkBoxRef = useRef()
    
const [name,setName] = useState('')
const user = useAuth()

const [cartQty,setCartQty] = useState(null)


const {data,loading} = useReadCart(null, user? user.uid : null)
const {logoff} = useLogoff()









const handleLogoff= () => {
logoff()
}


const toggle = (e) => {
    
    checkBoxRef.current.checked = false
}

useEffect(()=>{
    if(user){
    const {displayName:name} = user
    setName(name)
    }
    },[user])

useEffect(() => {
if(data){
    setCartQty(data.length)
}
},[data])

  return (
<header>

   <div className={styles.row1}>
        <Link to={`/`} className={styles.logo}>
            <img src={logo} alt="" />
        </Link>
        <div className={styles.primary}>
            <Search/>         
        </div>

        <div className={styles.user_container}>
            {user &&
                <Link to={"/cart"} className={`${styles.addCart_btn} btn__light`}>
                    {data && data.length!==0 &&
                        <span className={styles.cart_qty}  > {cartQty} </span>}
                        <i className="bi bi-cart"></i>
                </Link>}
            {user&&
                <div className={styles.user_btn}>
                    <i className={`bi bi-person ${styles.user_icon}`} >  </i>
                    <i className="bi bi-caret-down-fill"></i>
                    <div className={styles.user_submenu}>

                        < p>ol {name && name.split(' ')[0]}</p>

                        <ul className={styles.user_submenu_visible }>
                            <Link to={`/dashboard`} className={styles.user_submenu_item}>sua conta</Link>
                            <li className={styles.user_submenu_item} onClick={handleLogoff}>
                                <span>sair</span>
                                <i className="bi bi-box-arrow-right"></i>
                                </li>
                        </ul>
                    </div>
                </div>}
        </div>
   </div>
   {/* min-width 768px */}
   <div className={styles.primary}>
        <PrimaryNav/>
   </div>
   {/* below 768px resolution */}
    <div className={styles.toggle}>
        <ToggleNav/>
    </div>

</header>
  )
}

export default Header