import { useEffect, useLayoutEffect, useState } from 'react'
import { useNavigate } from 'react-router-dom'
import Card from '../../components/Card/Card'
import useSearchProducts from '../../hooks/dbHooks/useSearchProducts'
import useQuery from '../../hooks/useQuery'
import styles from './Search.module.css'
import Loader from '../../components/Loader/Loader'

const Search = () => {
   //capturar o width da tela
   const [screenWidth,setScreenWidth] = useState(window.innerWidth)


   const [products,setProducts] = useState([])
   const [urlQuery,setUrlQuery] = useState()

   const q = useQuery()
   const navigate = useNavigate()
   const query = q.get('q')

   const {data,loading,} = useSearchProducts(query)

   useEffect(()=>{
   if(data){
      setProducts(data)

   }
   },[data])

   




   const prodCategory = (categ) =>{

   let categoryQty = 0

   products.forEach((prod) =>{
      if(prod.category.toLowerCase() === categ){
         categoryQty += 1
      }

   })

   return categoryQty


}



return (
   <div>

      <section className={styles.search_container}>
         <div className={styles.filter}>
            <div className={styles.filter_item}> 
               <i class="bi bi-arrow-bar-up f-20 title_neon"></i>
               <i class="bi bi-arrow-bar-down f-20 title_neon"></i>
               <i class="bi bi-arrow-counterclockwise f-20 title_neon"></i>
            
            </div>
            
         </div>
         <div className={styles.products_container}>

            {products && 
            products.map((prods,idx) => {return <Card details={prods}/>})}
            { products.length===0 && !loading && <p className='normal-neon-font t-center'>Nada encontrado =c</p>}
            {loading && <Loader/>}

         </div>

      </section>




 
   </div>
    )

}

export default Search