//search-bar
import Search from '../../Search/Search'
//estilos
import styles from './ToggleNav.module.css'
//logo
import logo from '../../../assets/images/logo2.png'
import { useEffect, useRef, useState } from 'react'
const ToggleNav = () => {
  const [toggle,setToggle] = useState(true)

  return (
  <section className={styles.navContainer}>
    
    <div className={styles.toggleBtn} onClick={() => setToggle(true)}>
      <i className="bi bi-list"></i>
    </div>

      <nav className={`${styles.navigate} ${toggle? styles.toggleOn : styles.toggleOff}`}>
      {/* botao de fechar  */}
        <div className={`${styles.close} normal-neon-font`} onClick={() => setToggle(false)}>
          <i className="bi bi-x title_neon "></i>
        </div>
      {/* bg para fechar */}
      
      <div className={styles.logo}>
        <img src={logo} alt="" className={styles.image}/>
      </div>
        <Search/>         
    {/* ajustar o toggle_menu */}
        <ul className={`${styles.mainMenu} `}>
            <li className={`${styles.menuItem} normal-neon-font`}>categories</li>
            <li className={`${styles.menuItem} normal-neon-font`}>offers</li>
            <li className={`${styles.menuItem} normal-neon-font`}>PC</li>
            <li className={`${styles.menuItem} normal-neon-font`}>Keyboards</li>
        </ul>
        <ul className={styles.auth_container}>
            <li className={`${styles.menuItem} normal-neon-font`}>login</li>
            <li className={`${styles.menuItem} normal-neon-font`}>register</li>
        </ul>
      </nav>
  </section>
  )
}

export default ToggleNav