import { ref, child, push, update , getDatabase } from 'firebase/database'
import { useEffect, useState } from 'react'


const useAddData = () => {
    const [cancelled,setCancelled] = useState(false)

    const checkMemoryLeak = () => {
        if(cancelled) return;
    }
    const createData = async (data,uid) => {
     
        checkMemoryLeak()
        try {
            const db = getDatabase()
            const userData = {
                name:data.displayName,
                email:data.email,
                phone:''
            }
            const updates = {}
            updates[`users/${uid}/userData/metadata`] = userData
            update(ref(db),updates)
            
        } catch (error) {
            console.log('nao foi mano nao foi ')
        }
    }
    useEffect(() =>{
        return () => setCancelled(true)
    })
    return {createData}
}

export default useAddData