// hooks
import  { useEffect, useState } from 'react'
// firebase
import { ref, child, push, update , getDatabase } from 'firebase/database'

const useAddCartOrFavorite = () => {

const [cancelled,setCancelled] = useState(false)
 
function clearMemoryLeak () {
if(cancelled) {
return};}
    
    
const addCartOrFavorite= async (uid,details,collection) =>{
const {name,saller,url,price,parcels,category,subCategory} = details   

clearMemoryLeak()
  
try {
    const db = getDatabase()   
    const data={
        name,
        saller,
        price,
        parcels,
        url,
        category,
        subCategory,
        id:details.id
    }

    const updates = {}
    updates[`users/${uid}/${collection}/${details.id}`] = data
    update(ref(db),updates)

        } catch (error) {
            console.log('addcart',error.message)
        }
        
}
    
    
useEffect(()=>{
        
return ()=> setCancelled(true)    
    })
    
return {addCartOrFavorite}
}

export default useAddCartOrFavorite