import { child, get, getDatabase, onValue, ref } from "firebase/database"
import { useEffect, useState } from "react"

const useReadDoc = (collection) => {
const [data,setData] = useState(null)
const [cancelled,setCancelled] = useState(false)
const [loading,setLoading] = useState(false)
useEffect(()=>{


const fetchData = async () =>{
if(cancelled) return;
setLoading(true)
try {

    const dbRef= ref(getDatabase())
    const snapshot = await get(child(dbRef,!collection ? `products` : `products/${collection}`))
    const val = []
if(!collection){   for(let pos in snapshot.val()){
    val.push(snapshot.val()[pos])
   }
  setData(val)}
  else{
    val.push(snapshot.val())
    setData(val)
  }
} catch (error) {
    console.log('arquivo read',error.message)
    
}
setLoading(false)}

fetchData()
return () => setCancelled(true)
},[])

return {data,loading}



}

export default useReadDoc