import { getAuth, createUserWithEmailAndPassword,updateProfile } from "firebase/auth";



import { useEffect,useState } from 'react'
import {db} from "../firebase/config"
import useAddData from "./useAddData";
const useAuthentication = () => {
    // create loading state
    const [cancelled,setCancelled] =  useState (false) 
    const[loading,setLoading] = useState(false)

    const[error,setError] = useState (null)
    const {createData} = useAddData()
    function checkMemoryLeak () {
        if(cancelled){
            return;
        }
    }


    const auth = getAuth ()

    const createUser= async (data) => {

    setLoading(true)
    checkMemoryLeak()
    setError("")

   try {

    const {user}=await createUserWithEmailAndPassword (auth,data.email,data.password)
    await updateProfile (user,{displayName:data.displayName})
    await createData(data,user.uid)
    return user

   } catch (error) {
   
    let errorMessage;
console.log(error.message)
    if(error.message.includes('email')){
        errorMessage='Email invalido ou j existe'
    }

    else if(error.message.includes('Password')){
       
        errorMessage='a senha deve conter no mnimo 6 caracteres'
    }

    else{
errorMessage='Ocorreu algum erro. tente novamente mais tarde =)'}
   
    setError(errorMessage)
   } 

   setLoading(false)

}

useEffect(()=>{

return ()=> setCancelled(true)
},[])

return {createUser,error,auth,loading}
}

export default useAuthentication