import { useEffect, useRef, useState } from 'react'
import Card from '../../components/Card/Card'
import useInsertProduct from '../../hooks/dbHooks/useInsertProduct'
import useReadDoc from '../../hooks/dbHooks/useReadDoc'
import useUploadDoc from '../../hooks/dbHooks/useUploadDoc'

const Add = () => {
const [imgReference,setImgReference] = useState()
const {upData,docRef} = useUploadDoc()

const nameRef = useRef()
const priceRef = useRef ()
const sallerRef = useRef()
const parcelsRef = useRef()
const imgRef = useRef ()
const categoryRef = useRef()
const subCategoryRef = useRef()


const handleSubmit = async  (e) => {
e.preventDefault()

const file = {
    imgDetails:imgRef.current.files[0],
    name:nameRef.current.value,
    price:priceRef.current.value,
    saller:sallerRef.current.value,
    parcels:parcelsRef.current.value,
    category:categoryRef.current.value,
    subCategory:subCategoryRef.current.value,
}

upData(file)

nameRef.current.value = ''
priceRef.current.value = ''
sallerRef.current.value = ''
parcelsRef.current.value = ''
imgRef.current.value= ''
categoryRef.current.value= ''
subCategoryRef.current.value= ''

}

useReadDoc()




return (
<>

<form onSubmit={handleSubmit}>
    <label >
        <input type="text" name="name" placeholder='name' ref={nameRef} />
    </label>
    <label >
        <input type="text" name="name" placeholder='saller' ref={sallerRef}/>
    </label>
    <label >
        <input type="number" name="name" placeholder='price'  ref={priceRef}/>
    </label>
    <label >
        <input type="number" name="name" placeholder='PARCELS' ref={parcelsRef}/>
    </label>
    <label >
        <input type="file" name="name" placeholder='img' ref={imgRef} required />
    </label>
    <select name="sel"  ref={categoryRef} required>
        <option value="">categorias</option>
        <option value="CPU">CPU</option>
        <option value="perifericos">perifericos</option>
        <option value="processadores">processadores</option>
        <option value="armazenamento">armazenamento</option>
        </select>
    <select name="sel"  ref={subCategoryRef} required>
        <option value="">Subcategorias</option>
        <option value="computador">computador</option>
        <option value="mouse">mouse</option>
        <option value="Teclado">teclado</option>
        <option value="processador">processador</option>
        <option value="memoria">memoria</option>
        </select>
    
    <button>enviar</button>
    </form>
    <button onClick={() => console.log(imgReference)}>click</button>

    
    </>

)

    

}

export default Add