import { child, getDatabase, ref, remove } from "firebase/database"
import { useEffect, useState } from "react"


const useRemoveCarOrFav = (uid,collection,proID) => {

const [ cancelled,setCancelled] = useState(false)


function clearMemoryLeak () {
    if(cancelled) return;
}

const removeCartOrFav = (uid,collection,proID) => {
clearMemoryLeak()

try {
const dbRef = ref(getDatabase())
remove(child(dbRef,`users/${uid}/${collection}/${proID}`))
console.log('eu amo minha mocinha')
} catch (error) {
    console.log('ihhh deu erro irmao')
    
}
}

useEffect(() => {

return () => setCancelled(true)    
})

return {removeCartOrFav}
}

export default useRemoveCarOrFav