import { child, get, getDatabase, onValue, ref } from "firebase/database"
import { useEffect, useState } from "react"


const useSearchProducts = (query) => {

    const [data,setData] = useState(null)
    const [cancelled,setCancelled] = useState(false)
    const [loading,setLoading] = useState(false)

    useEffect(()=>{
        const fetchData = async () =>{
            
            setCancelled(false)
            if(cancelled) return;
            setLoading(true)
            try {
                const dbRef= ref(getDatabase())
                const snapshot = await get(child(dbRef,`products`))
                const val = []
                const searchParams = ['name','subCategory','category','saller'] //  keywords for search

                for(let item in snapshot.val()){
                    for(let params of searchParams){

                        if(snapshot.val()[item][params].toLowerCase().includes(query.toLowerCase())){
                            val.push(snapshot.val()[item])
                            break
                        
                        }
                    
                    }

                }
                setData(val)}
                catch (error) {
                    let errorMessage;
                    if(error.message.includes('Cannot read properties')){
                        errorMessage = 'nenhum produto encontrado =C'
                    }

            console.log(errorMessage)
                
            }
            setLoading(false)
        }

        fetchData()
    return () => setCancelled(true)
    },[query])

    return {data,loading}

}

export default useSearchProducts