// hooks
import  { useEffect, useState } from 'react'
// firebase
import { database } from '../../firebase/config'
import { ref, child, push, update , getDatabase } from 'firebase/database'



const useInsertProduct = () => {

const [cancelled,setCancelled] = useState(false)

function clearMemoryLeak () {
    if(cancelled) {
   
        return};
}


const insertProduct = async (details,url) =>{
//alterei tirei esses valores abaixo do parametro e dei o destructuring
const {name,saller,price,parcels,category,subCategory} = details

    try {
    const db = getDatabase()
    

    
    const newProductsKey = push(child(ref(db),`products`)).key
    const data={
        name,
        saller,
        price,
        parcels,
        url,
        category,
        subCategory,
        id:newProductsKey
    }
    const updates = {}
    updates[`products/${newProductsKey}`] = data
    update(ref(db),updates)

    } catch (error) {
        console.log(error.message)
    }
    
 
    
    
    }


useEffect(()=>{
    
return ()=> setCancelled(true)    
})

return {insertProduct}

}

export default useInsertProduct