// hooks
import { getAuth } from 'firebase/auth'
import { useEffect, useRef,useState } from 'react'
import useLogin from '../../hooks/useLogin'
// router
import {Link } from 'react-router-dom'
// styles
import styles from './Login.module.css'

const Login = () => {
const [error,setError]=useState('')

const emailRef =useRef()
const passwordRef =useRef()
 

const {userLogin,error:systemError} = useLogin ()

    
const handleSubmit = async (e) => {
e.preventDefault()

const user={
    email:emailRef.current.value,
    password:passwordRef.current.value
}

await userLogin(user)



}


useEffect(()=>{
if(systemError){
    console.log('oi')
    setError(systemError)
}
})

  return (
    <section className={styles.login}>
    <div className={styles.login__section}>
        
            <div className={styles.login__title}>
                <h1>Entrar na conta</h1>
                      <p>Por favor, preencha seus dados corretamente.</p>
            </div>
        
            <div className={styles.login__container}>
                <form
                onSubmit={handleSubmit}
                 className={styles.form_login__authentication}>
        
              
                <label className={styles.form_login__item} >
                    <span>Email</span>
                    <input type="email" name="email" required  ref={emailRef} className={styles.form_login__input} autoComplete='off'  />
                </label>
                <label  className={styles.form_login__item}>
                    <span>Password</span>
                    <input type="password" name="name" required  ref={passwordRef}  className={styles.form_login__input}/>
                </label>
              
        
                   <button className={`${styles.form_login__btn} btn__dark`}>Login</button>
                </form>
               
            { error && <p className={styles.login__message_error}>{error}</p>}
        
            </div>
            <Link to={`/register`} className={styles.createAccount}>crie sua conta </Link>
    </div>
    
        </section>
  )
}

export default Login