import { getDatabase,update,ref,child,push } from "firebase/database"
import { useAuth } from "../../context/AuthContext"
import { useEffect, useState } from "react"



const useUpdateInfo = () => {
  const [error,setError] = useState(null)
  const [loading,setLoading] = useState(null)
  const user = useAuth()
  
   const upData = async (data) => {
    const {email,name,phone} = data
    try {
        const db = getDatabase()
        const userData = {
        email,
        name,
        phone
        }
        const updates = {};
        updates[`/users/${user.uid}/userData/metadata`] = userData;
        return update(ref(db),updates);
        
    } catch (error) {
        console.log('deu ruim no update',error.message)
    }


   }
return {upData}
}

export default useUpdateInfo