// hooks
import {useRef,useState,useEffect} from 'react'
import useRegister from '../../hooks/useRegister'
// styles
import styles from './Register.module.css'
const Register = () => {

const [error,setError]=useState('')


const nameRef =useRef()
const emailRef =useRef()
const passwordRef =useRef()
const confirmPasswordRef =useRef()

const {createUser,error:systemError,loading}=useRegister()



const handleSubmit=async (e)=>{
e.preventDefault()
setError("")


if(passwordRef.current.value!==confirmPasswordRef.current.value){
    setError('Confirme a senha corretamente !')
    return
}



const user={
    email:emailRef.current.value,
    password:passwordRef.current.value,
    displayName:nameRef.current.value
}


const {user:res}=await createUser(user)
  console.log(res)
}

useEffect(()=>{
if(systemError){
    setError(systemError)
}
},[systemError])



  return (


      
    <section className={styles.register}>
      
<div className={styles.register__section}>
    
        <div className={styles.register__title}>
            <h1>Criar conta</h1>
                  <p>Crie sua compra e comee ja a comprar e vender seus produtos gamer.</p>
             </div>
    
        <div className={styles.register__container}>

            <form onSubmit={handleSubmit}className={styles.form_register__authentication}>
    
            <label className={styles.form_register__item} >
                <span className={styles.icon}><i className="bi bi-person"></i></span>
                <span className={styles.register__dataName}>Name</span>
                <input type="text" name="name"  required  ref={nameRef} className={styles.form_register__input} autoComplete='off' />
            </label>
            <label className={styles.form_register__item} >
                <span className={styles.icon}><i className="bi bi-envelope"></i></span>
                <span className={styles.register__dataName}>Email</span>
                <input type="text" name="email" required  ref={emailRef} className={styles.form_register__input} autoComplete='off'   />
            </label>
            <label  className={styles.form_register__item}>
                <span className={styles.icon}><i className="bi bi-lock-fill"></i></span>
                <span className={styles.register__dataName}>Password</span>
                <input type="password" name="name" required  ref={passwordRef}  className={styles.form_register__input}/>
            </label>
            <label className={styles.form_register__item} >
                <span className={styles.icon}><i className="bi bi-lock-fill"></i></span>
                <span className={styles.register__dataName}>Confirm Password</span>
                <input type="password" name="confirmPassword"  required ref={confirmPasswordRef} className={styles.form_register__input} />
            </label>
    
               {!loading &&<button className={`${styles.form_register__btn} btn__dark`}>Register</button>}
               {loading && <button className={`${styles.form_register__btn} btn__dark`} disabled>Aguarde...</button>}
            </form>
        { error && <p className={styles.register__message_error}>{error}</p>}
    
        </div>
</div>

    </section>
  )
}

export default Register