// swipper
import {Swiper,SwiperSlide} from 'swiper/react'
import {A11y, Autoplay, FreeMode, Keyboard, Navigation, Pagination} from 'swiper'
import 'swiper/css';
import 'swiper/css/pagination'
import 'swiper/css/navigation'


// images
import promo1 from '../../assets/images/BannerPromo.png'
import promo2 from '../../assets/images/Banner2.png'
// styles
import styles from './Home.module.css'
// components
import Card from '../../components/Card/Card'
// hooks
import { useEffect, useState, useRef } from 'react'
import useReadDoc from '../../hooks/dbHooks/useReadDoc'
//React Router
import { Link } from 'react-router-dom'
import Loader from '../../components/Loader/Loader';


const Home = () => {
  
  const [products,setProducts] = useState([])

  const {data,loading} = useReadDoc()

  const navPrevRef = useRef()
  const navNextRef = useRef()
  const swiperRef = useRef()

  useEffect(()=>{
    if(data){
  setProducts(data)
  console.log(data)
    }
  },[data])





  return (
 <section className={styles.home}>
{loading && <Loader/>}

<section className={styles.offers}>
{!loading &&
<><Swiper
      ref={swiperRef}
      freeMode={true}
      keyboard={true}
      grabCursor={true}
      autoplay={{delay: 4000} }
      navigation = {{ 
      nextEl: navNextRef.current,
      prevEl: navPrevRef.current,}}
      centeredSlides={true}
      loop={true}
      pagination={{clickable:true}}
      slidesPerView={1}
      modules={[FreeMode,Keyboard,Autoplay,Navigation,Pagination,A11y]}>

      <SwiperSlide>
        <Link to={"/"} className={styles.image_box}><img src={promo2} alt="promoo" aria-label='banner promocional' className={styles.image} /></Link>
      </SwiperSlide>

      <SwiperSlide>
        <Link to={"/"} className={styles.image_box}>
          <img src={promo1} alt="promoo" aria-label='banner promocional'  className={styles.image} /></Link>
      </SwiperSlide>
     
    </Swiper>
    <div className={styles.slide_controls}>
        <span 
        className={`${styles.btn_left} ${styles.btn_manual} `}
        ref={navPrevRef}>
          <i className="bi bi-caret-left-fill"></i></span>
        <span 
        className={`${styles.btn_right} ${styles.btn_manual} `}
        ref={navNextRef}
        >
          <i className="bi bi-caret-right-fill"></i></span>
      </div></>}
 
</section>



{!loading&&<section className={styles.products}>
  
<h2>Ofertas  <span>ver todas</span></h2>
  <Swiper // esse  o container
    freeMode={true}
    grabCursor={true}
    modules={[FreeMode]}
    className={styles.products__container}
    spaceBetween={2}
    slidesPerView={3}

    breakpoints={{
    0:{slidesPerView:1.1},
    500:{slidesPerView:1.3},
    600:{slidesPerView:1.8},
    800:{slidesPerView:2.5},
    1080:{slidesPerView:3.5},
    1400:{slidesPerView:4.3}}}>


  {products && products.map((prods,idx) => {
    if(idx<=6){
      return (
      <SwiperSlide key={prods.name}>
        <Card details={prods}/>
      </SwiperSlide>)}}
  )}

      </Swiper>
</section>}

 </section>
  )
}

export default Home