import styles from './Card.module.css'
import { Link,useNavigate} from 'react-router-dom'
import { useState } from 'react'
import { useAuth } from '../../context/AuthContext'
import useAddCartOrFavorite from '../../hooks/dbHooks/useAddCartOrFavorite'
const Card = ({details}) => {
const user = useAuth()
const [cartState,setCartState] = useState("bi bi-cart") 
const [favoriteState,setFavoriteState] = useState("bi bi-heart") 
const parc =  (Number(details.price)/Number(details.parcels)).toFixed(2)

const navigate = useNavigate()

const {addCartOrFavorite} = useAddCartOrFavorite()

const cartOrFavorite = (btn) =>{

if(!user){
navigate('/login')
return;
}

addCartOrFavorite(user.uid, details, btn)

if(btn === 'cart')
{
  cartState? setCartState(false) : setCartState(true)

}

else if(btn === 'favorite')
{favoriteState? setFavoriteState(false) : setFavoriteState(true)}

}





  return (
    <div className={styles.products__item} >
    <div className={styles.product__image}>
    <img src={details.url} alt="" className={styles.product_img}/>
  </div>

    <div className={styles.product__details}>
    
    <div className={styles.product__details__title}>
      <h5>{details.name}</h5>
      <span>{details.saller}</span>
    </div>
  <div className={styles.product__details_price}>
    <p><span>$</span> {details.price}</p>
    <span>em at {details.parcels}x {parc}</span>
  </div>

  </div>

  <div className={styles.actions}>
    <Link to={`/details/${details.id}`} className="btn__dark title_small ">comprar</Link >


   
  <button 
  className={`${styles.btn_icon} btn__light`} 
  onClick={()=>cartOrFavorite('cart')}>
  <i className={cartState? "bi bi-cart" : "bi bi-cart-fill"}></i></button>

    <button  className={`${styles.btn_icon} btn__light`} onClick={()=>cartOrFavorite('favorite')}><i className={favoriteState? "bi bi-heart" : "bi bi-heart-fill"}></i></button>
    
  </div>
  
</div>
  )
}

export default Card