import styles from './PrimaryNav.module.css'

const PrimaryNav = () => {
  return (
    <nav className={styles.navigate}>
      <ul className={`${styles.mainMenu} `}>
        <li className={`${styles.menu_item} normal-neon-font`}>categories</li>
        <li className={`${styles.menu_item} normal-neon-font`}>offers</li>
        <li className={`${styles.menu_item} normal-neon-font`}>PC</li>
        <li className={`${styles.menu_item} normal-neon-font`}>Keyboards</li>      
      </ul>
      <div className={styles.auth_container}>
        <li className={`${styles.menu_item} normal-neon-font`}>login</li>
        <li className={`${styles.menu_item} normal-neon-font`}>register</li>
      </div>

    </nav>
  )
}

export default PrimaryNav