import styles from './Dashboard.module.css'
import no_image from '../../assets/images/no-user-image.png'
import { useAuth } from '../../context/AuthContext'
import { useEffect, useState } from 'react'
import {Link} from 'react-router-dom'
import useReadInfo from '../../hooks/userHooks/useReadInfo'
const Dashboard = () => {

const user = useAuth()
const [message,setMessage] = useState('Necessrio Prenchimento ')
const [userData,setUserData] = useState('')

const {userData:data} = useReadInfo()

useEffect(() => {
if(userData){
  console.log(data)
  setUserData(data)
}
},[data])


return (
<section className={styles.dashboard}>

<div className={styles.main_container}>

<div className={`${styles.user_stats} ${styles.container_item} `}>

<div className={styles.details}>

<div className={styles.image_box}>

<div className={styles.user_image}>
  <img src={no_image} alt=""  className={styles.user_img}/>
</div>

<div className={styles.name}>
  <h5 className='title_neon-2r'>{user.displayName}</h5>
  <p className='font-14'>iniciante</p>
</div>
  

</div>



<Link to={`/profile/edit`} className="btn__light">Edit Profile
</Link>
</div>



<div className={styles.contacts}>
<h5 className='title_neon-2r'>Stats</h5>

<div className={styles.contact_item}>
<i className="bi bi-geo-alt"></i>
<span className='font-14'> {user.address? (user.address) : (message)}</span>
</div>

<div className={styles.contact_item}>
  <i className="bi bi-envelope"></i>
<span className='font-14'> {user.email}</span>
</div>

<div className={styles.contact_item}>
<i className="bi bi-phone"></i>
<span className='font-14'>  {user.phoneNumber? (user.phoneNumber) : (message)}</span>
</div>

<div className={styles.contact_item}>
<i className="bi bi-phone"></i>
<span className='font-14'> {user.age? (user.age) : (message)}</span>
</div>

</div>

  </div>
  
<div className={styles.user_data}>
<div className={`${styles.user_points}  ${styles.short} pad-20`}>
<h5 className='title_neon-2r font-14'>Anker Points</h5>
<p>pontos aqui</p>

</div>
    
<div className={`${styles.user_balance} ${styles.short} pad-20 `}>
<h5 className='title_neon-2r font-14'>Balance:</h5>

<p>saldo aqui</p>
</div>

<div className={`${styles.user_boughts} ${styles.large} pad-20 `}>
<h5 className='title_neon-2r font-14'>boughts</h5>

<p>o item comprado <span>hora que foi comprado</span></p> 
</div>
</div>
</div>

        
 </section>
  )
}

export default Dashboard