import { useEffect, useState } from "react"

//router
import {Link} from 'react-router-dom'

//styles
import styles from './EditProfile.module.css'
//image
import no_image from '../../assets/images/no-user-image.png'
// components
import Accounts from "../../components/user Details Display/Accounts/Accounts"
import Compras from "../../components/user Details Display/Compras/Compras"
import History from "../../components/user Details Display/History/History"
import useReadData from "../../hooks/dbHooks/useReadData"
import { useAuth } from "../../context/AuthContext"



const EditProfile = () => {

  const user = useAuth()
  const [products,setProducts] = useState([])
  const [view, setView] = useState('account')
  const [userData,setUserData] = useState('')

  const {data,loading} = useReadData(user.uid)

//checkar no db se existe o campo foto de perfil caso exista exibir, se nao, exibir a foto do usuario nao encontrado

//a parte de info controller vai conter, account que vai ser um icone de engrenagem, historico de visitas um relojinho , compras moedinhas

//componentizar e mudar o display com state

  useEffect(() => {
    if(data) {
      setUserData(data)
    }
  },[data])

return (
<section className={styles.edit_profile}>
  <div className={styles.user}>

  <div className={styles.user_details}>

    <div className={styles.image_box}>
      <div className={styles.change_pic}>
        <i className={`${styles.cam_icon} bi bi-camera `} ></i>
        <span>edit</span></div>
      <img src={no_image} alt="" />
    </div>
  <div className={`${styles.name} pos-r`}>
    <span>Name</span>
    <Link to={`/dashboard`}>
    <i className={`${styles.back_icon} bi bi-box-arrow-left title_neon-2r`}></i>
    </Link> </div>
  </div>

  </div>


  <div className={styles.info_controller}>

  <div className={styles.ctrl_btn} onClick={()=> setView('account')}>
    <i className="bi bi-person-gear title_neon-2r"></i>
    <span className={styles.show_option}>Account</span>
  </div>
  <div className={styles.ctrl_btn} onClick={()=> setView('history')}>
    <i className="bi bi-clock-history title_neon-2r"></i>
    <span className={styles.show_option}>History</span>
  </div>
  <div className={styles.ctrl_btn}onClick={()=> setView('compras')}>
    <i className="bi bi-bag title_neon-2r"></i>
    <span  className={styles.show_option} >Compras</span>
  </div>
  
  </div>      

<div className={styles.info_display}>
  
{view ==='account' && <Accounts userData={userData[0]}></Accounts>}
{view ==='history' && <History></History>}
{view ==='compras' && <Compras></Compras>}

  </div>        


  </section>
  )
}

export default EditProfile