import { child, push, update , getDatabase } from 'firebase/database'
import { getStorage , ref,uploadBytesResumable, getDownloadURL } from "firebase/storage"
import { useEffect, useState } from "react"
import useInsertProduct from './useInsertProduct'



const useUploadDoc = () => {
    const [progress,setProgress] = useState(null)
    const [docRef, setDocRef] = useState(null)
    const [ cancelled, setCancelled] = useState(false)

    const {insertProduct} = useInsertProduct()

const checkMemoryLeak = () =>{
    if(cancelled)return;
}
    const upData = async (details) => {
checkMemoryLeak()
        try {
            const storage = getStorage()

            const storageRef = ref(storage,`products/${details.imgDetails.name}`)
            const metadata = {
                contentType:'image/png'
            }
        
            const uploadTask = uploadBytesResumable(storageRef,details.imgDetails,metadata)
            const snapshot = await uploadTask
           const url = await getDownloadURL(snapshot.ref)
     
           insertProduct(details,url)
        
        } catch (error) {
            console.log(error.message)
        }

    }



useEffect(()=>{
        
    return ()=> setCancelled(true)

},[])
return {upData,docRef,progress}

}

export default useUploadDoc