  
import './App.css';
import Header from './components/Header/Header';
import {BrowserRouter,Routes,Route, Navigate,useParams} from 'react-router-dom'
import Register from './pages/Register/Register';
import Home from './pages/Home/Home';
import Login from './pages/Login/Login';
import{UseAuthContextProvider} from './context/AuthContext'
import { getAuth, onAuthStateChanged } from 'firebase/auth';
import { useEffect, useState } from 'react';
import useAuthentication from './hooks/useRegister';


import Add from './pages/ADDDB/Add';
import Search from './pages/Search/Search';
import About from './pages/about/About';
import Details from './pages/Details/Details';
import Cart from './pages/Cart/Cart';
import Dashboard from './pages/Dashboard/Dashboard';
import EditProfile from './pages/EditProfile/EditProfile';
import Footer from './components/Footer/Footer';
import Purchase from './pages/Purchase/Purchase';
import Loader from './components/Loader/Loader';


function App() {
const [user,setUser] = useState(undefined)

const loading = user === undefined


const auth= getAuth()

useEffect(()=>{

onAuthStateChanged(auth,( user ) =>{
setUser(user)
})  

},[])


if(loading){
  return <Loader/>
}

return (
    <div className="App">



<UseAuthContextProvider value={user}  >
<BrowserRouter>
<Header/>
<Routes>
<Route path='/' element={<Home/>}/>
<Route path='/register' element={ user ? <Navigate to={`/`}/> : <Register/>}/>
<Route path='/login' element={ user ? <Navigate to={`/`}/> : <Login/>}/>
<Route path='/dashboard' element={user? <Dashboard/> : <Navigate to ={`/login`}/>}/>
<Route path='/add' element={<Add/>}/>
<Route path='/search' element = {<Search/>}/>
<Route path='/about' element = {<About/>}/>
<Route path='/details/:id' element = {user ? <Details/> : <Navigate to={'/login'}/>}/>
<Route path='/cart' element = {user ? <Cart/> : <Navigate to={'/login'}/>}/>
<Route path='/purchase' element = {user ? <Purchase/> : <Navigate to={'/login'}/>}/>
<Route path='/profile/edit' element = {user ? <EditProfile/> : <Navigate to={'/login'}/>}/>

<Route path='*' element={<Navigate to={"/"}/>}/>

</Routes>

<Footer/>


</BrowserRouter>

</UseAuthContextProvider>


    </div>
  );
}

export default App;
