import { useEffect, useRef, useState } from 'react'
import { Link } from 'react-router-dom'
import { useAuth } from '../../context/AuthContext'
import useReadCart from '../../hooks/dbHooks/useReadCart'
import useRemoveCarOrFav from '../../hooks/dbHooks/useRemoveCarOrFav'
import styles from './Cart.module.css'

const Cart = () => {

  const[cartProds,setCartProds] = useState([])
  const [prodQty,setProdQty] = useState([])
  const [total, setTotal] = useState( )

  const user = useAuth()

  const {removeCartOrFav} = useRemoveCarOrFav()
  const {data,loading} = useReadCart(null,user.uid)



  const handleDelete = (prodID) => {
  removeCartOrFav(user.uid,'cart',prodID)
  }

  const addOrSubQty =  (action,productKey) => {


  if(action ==='add') {
    if(prodQty[productKey] >= 99) return
  setProdQty((previus) => {
  previus[productKey]+=1
  return {...previus}

  })
  return;
  }
    
  if(prodQty[productKey] === 0) return;

  setProdQty((previus) => {
  previus[productKey]-=1
  return {...previus}

  })
  return;
    


  }

  const changeTotal = () => {
    console.log('oi')
  }
//setando o state dos produtos e separando a quantidade pares id/qty
  useEffect(()=>{
      if(data) {
        console.log(data)
        setCartProds(data)
        const quanty = {}
        for( let pos of data) {
          quanty[pos.id] = 1
        }
        setProdQty(quanty)
      }
  },[data])

  useEffect(() => {
  let val = 0
  for(let prods of cartProds){
  val += prodQty[prods.id] * prods.price
  }
  setTotal(val)
  },[prodQty])


  return (
  <section className={styles.user_cart}>
    

    <div className={styles.cart_container}>
    {loading && <div className='load'></div>}    
    <div className={styles.cart_title}>
  <h3 className='title_neon  yellow-theme'>Carrinho de Compras</h3>
  {/* <span>desmarcar todos os itens</span> */}
</div>
    
    {cartProds.length===0 && <p> carrinho vazio...</p>}
    
    {cartProds.length !==0 &&
    cartProds.map((product) => {
    const parcels = (Number(product.price)/Number(product.parcels)).toFixed(2)
    
     return <div className={styles.product_info}>
    <div className={styles.image_box}>
    <img src={product.url} alt={product.name} className={styles.product_img}/>
    </div>
    
    <div className={styles.product_details}>
    <h4 className='title_neon-2r yellow-theme'>{product.name}</h4>
    <p><span>em estoque</span></p>
    <p>vendedor: {product.saller}</p>
    <div className={styles.qty}>
    
      <label className={styles.qty_btn} >
        <input type="text"   disabled   value={prodQty[product.id]} className={styles.qty_input} onChange = {changeTotal} />
      </label>
    <div className={styles.qty_controls}>
      <i className="bi bi-plus-circle"  onClick={()=> addOrSubQty('add',product.id)}></i>
      <i className="bi bi-dash-circle" onClick={()=> addOrSubQty('sub',product.id)}></i>
      
      </div>
     
    
    </div>
    
    <div className={styles.crud}>
    {/* <Link to={`/details/${product.id}`} className="btn__dark">comprar</Link > */}
    <button  className={`${styles.crud_icon} btn__light yellow-theme`} >
    <i className="bi bi-trash" onClick={() => handleDelete(product.id)}></i></button>
    </div>
    
    </div>
    
    <div className={styles.price_details}>
    <p>${product.price}</p>
    <p>em at ${parcels} sem juros</p>
    
    </div>
    </div>
    })}
 
   

      </div>

         
 {cartProds.length !==0 &&
<div className={styles.proceed_buy_box}>
    <div className={styles.proceed}>
      <h1 className='prod_title title_neon-2r'>subtotal da compra</h1>
      <p className='title_neon-2r'>{total} $</p>    {/*retirar o estilo inline*/}
      <Link to={`/purchase`} className='btn__cart neon-hover'>finalizar compra</Link>
    </div>
    
</div>}


    
  </section>
  )
}

export default Cart