//css
import styles from './Footer.module.css'
//images
import logo from '../../assets/images/Logo.png'
import { useEffect, useState } from 'react'

const Footer = () => {

  return (
   
    <footer className={styles.footer}>

        <div className={styles.logo_container}>
            <img src={logo} alt="logotipo" aria-label='logotipo' className={styles.image}/>
            <h2>Anker Company</h2>
        </div>

        <p>Este site consiste em uma E-commerce futuristico, foi utilizado ReactJS(front-end) e Firebase(back-end). Criado e editado por &copy;2023 Andr Pinheiro

        </p>

        <div className={styles.contacts}>
                <div className={styles.contact_item}>
                    <i className="bi bi-globe"></i>
                </div>
                <div className={styles.contact_item}>
                    <i className="bi bi-instagram"></i>
                </div>
                <div className={styles.contact_item}>
                    <i className="bi bi-github"></i>
                    <p><a href="https://github.com/DecoXD"></a></p>
                </div>
                <div className={styles.contact_item}>
                    <i className="bi bi-linkedin"></i>
                    <p><a href="https://github.com/DecoXD"></a></p>
                </div>
            </div>
         
                
    </footer>
  )
}

export default Footer