import { useEffect, useRef, useState } from 'react'
import useFetchCEP from '../../hooks/CEP/useFetchCEP'
import styles from './Purchase.module.css'

const Purchase = () => {

    const [address,setAddress] = useState(null)  
    const [bairro,setBairro] = useState('')  
    const [logra,setLogra] = useState('')  
    const [location,setLocation] = useState('')  
    const [uf,setUf] = useState('')  
   
    const [CEP,setCEP] = useState('')  
    const {fetchCep,address: userAddress} = useFetchCEP()
    
    const showAddressDetails = () => {
        setBairro(address.bairro)
        setLogra(address.logradouro)
        setLocation(address.localidade)
        setUf(address.uf)
    }

    const handleSubmit = (e) => {
        e.preventDefault()
        console.log(address)
        
    }
    useEffect(() =>{
        const fetchCepData = async () => {
            try {
                 if(CEP.length==8){
                    await fetchCep(CEP)
               }
                else{
                    setAddress(null)}
            } catch (error) {
                console.log(error.message)
            }
        }
        fetchCepData()
       
    },[CEP])
    useEffect(() =>{
       if(userAddress ){ setAddress(userAddress);}
       if(address)
       { showAddressDetails()}

        
    },[userAddress,address
    ])
 
  return (
    <section className={styles.mainContainer}>
        <h2>Finalizar compra</h2>

        <div className={styles.container}>
            <form className={styles.purchase_form} onSubmit={handleSubmit}>
             

                <label className={styles.form_item}>
                    <span>Nome completo</span>
                    <input type="text" name="name" id="" />
                </label>

                <label className={styles.form_item}>
                    <span>Telefone</span>
                    <input type="text" name="phone" id="" />
                </label>

                <label className={styles.form_item}>
                    <span>CEP</span>
                    <input type="text" name="cep" id="" onChange={(e) => setCEP(e.target.value)}/>
                </label>

                <label className={styles.form_item}>
                    <span>Pais/regiao</span>
                    <input type="text" name="" id="" />
                </label>

                <label className={styles.form_item}>
                    <span>Bairro</span>
                    <input type="text" name="" id="" value={bairro} onChange={(e) => setBairro(e.target.value)}/>
                </label>

                <label className={styles.form_item}>
                    <span>Estado</span>
                    <input type="text" name="" id="" value={location} onChange={(e) => setLocation(e.target.value)}/>
                </label>

                <label className={styles.form_item}>
                    <span>Cidade</span>
                    <input type="text" name="" id="" value={uf} onChange={(e) => setUf(e.target.value)} />
                </label>
                
                <label className={styles.form_item}>
                    <span>Complemento(opcional)</span>
                    <input type="text" name="" id="" value={logra} onChange={(e) => setLogra(e.target.value)} />
                </label>

               

                <label className={styles.form_item}>
                    <span>Numero da residncia</span>
                    <input type="text" name="" id="" />
                </label>

                

                <label className={ styles.check}>
                    <span>Tornar este endereo Padro</span> <input type="checkbox" name="" id="" />
                </label>

                <button className={`${styles.submit} neon-effect`}><i className="bi bi-arrow-right-circle-fill"></i></button>
            </form>

        </div>
    </section>
  )
}

export default Purchase