import { useState } from 'react'
import styles from './Search.module.css'
import { useNavigate } from 'react-router-dom'

const Search = () => {
    const [searchRef,setSearchRef] = useState('')
    const navigate = useNavigate()
    const handleSearch = (e) => {
        e.preventDefault()
        const query = searchRef
        if(query){
        navigate(`/search?q=${query}`)
        
        setSearchRef('')
        }
        }

    return (
        <div className={styles.form_container}>
        <form onSubmit={handleSearch}>
            <div className={styles.search_container}>
                <input type="text" name="search" value={searchRef} placeholder='Buscar produtos, Marcas e muito mais...' onChange={(e)=>setSearchRef(e.target.value)} autoComplete ='off'  required/>
                <button className={styles.search_btn}><i className="bi bi-search"></i></button>
            </div>
        </form>
    </div>
    )
}

export default Search